.profileMainContainer {
	background: rgba(54, 34, 8, 0.74);
	height: 900px;
	border-radius: 10px;
	border: dashed 2px white;
}

.book {
	height: 670px;
	width: 600px;
	background: rgba(228, 228, 101, 0.719);
	border: 5px dashed white;
	justify-content: center;
	margin-top: 10px;
	margin-bottom: 50px;
	margin-left: auto;
	margin-right: auto;
	border-radius: 10px;
}

.bookCover {
	height: 670px;
	width: 600px;
	background: rgba(228, 228, 101, 0.719);
	border: 5px dashed white;
	margin-top: 10px;
	margin-bottom: 50px;
	margin-left: auto;
	margin-right: auto;
	border-radius: 10px;
}

.messageBeneathBooks {
	color: white;
	margin-top: 100px;
	line-height: 200%;
}

.messageWhenNoContentOnBook {
	color: rgb(94, 87, 67);
	font-size: 24px;
	margin-top: 30px;
}

.editPageTitle {
	text-align: center;
}

.inputEditText {
	color: white;
}

.flipPageComponent {
	display: flex;
	margin-left: 40px;
	border: 4px solid white;
}

.articleContent {
	text-align: left;
	margin-left: 20px;
	margin-right: 20px;
	color: rgb(49, 47, 47);
	font-size: 20px;
}

.pageContent {
	text-align: left;
	margin-left: 20px;
}

.bookContent {
	font-size: 20px;
	color: white;
	text-shadow: 2px 2px #4e370b;
	margin-top: 20px;
}

.openBookBtn {
	margin-top: 80px;
	margin-left: 5px;
	padding: 10px 20px 10px 20px;
	border-radius: 10px;
	background: rgb(126, 99, 9);
	color: white;
	font-size: 18px;
	text-shadow: 2px 2px #000000;
	font-weight: bolder;
}

.deleteBtn {
	margin-left: 5px;
	padding: 10px 20px 10px 20px;
	border-radius: 10px;
	background: rgb(173, 21, 10);
	color: white;
	font-size: 14px;
	text-shadow: 2px 2px #000000;
	font-weight: bolder;
}

.openBookEmptyPageBtn {
	/* margin-top: 80px; */
	margin-left: 5px;
	padding: 10px 20px 10px 20px;
	border-radius: 10px;
	background: rgb(126, 112, 53);
	color: white;
	font-size: 18px;
	text-shadow: 2px 2px #000000;
	font-weight: bolder;
}

.modal-backdrop {
	background-color: transparent;
}

.modal {
	background-color: rgba(255, 255, 255, 0);
	width: 100%;
	min-height: 1000px;
}

.modal-content {
	background-color: rgb(15, 13, 2);
	color: rgb(253, 250, 250);
}

.openBookEditPageBtn {
	margin-top: 20px;
	margin-left: 5px;
	padding: 10px 20px 10px 20px;
	border-radius: 10px;
	background: rgb(32, 150, 21);
	color: white;
	font-size: 14px;
	text-shadow: 2px 2px #000000;
	font-weight: bolder;
}

.bookEmptyPageBtn {
	margin-left: 5px;
	padding: 10px 20px 10px 20px;
	border-radius: 10px;
	background: rgb(126, 112, 53);
	color: white;
	font-size: 14px;
	text-shadow: 2px 2px #000000;
	font-weight: bolder;
}

.profileTitle {
	color: white;
	text-shadow: 4px 4px #4e370b;
	font-size: 40px;
}

.bookInput {
	background: rgb(158, 139, 98) !important;
	height: 470px !important;
	padding: 12px 20px;
	border-radius: 10px;
	border: dotted 2px rgb(54, 38, 7);
	width: 90%;
	margin: auto;
	font-size: 20px;
	color: white;
	text-align: justify;
}

.saveButton {
	padding: 10px 40px 10px 40px;
	font-size: 20px;
	border-radius: 15px;
	background: rgb(241, 121, 35);
	color: white;
	text-shadow: 2px 2px #4e370b;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
	.bookCover {
		width: 300px;
		margin-left: -5px;
	}
	.flipPageComponent {
		margin-left: 10px;
	}
	.profileTitle {
		font-size: 26px;
	}
	.deleteBtn {
		float: left;
	}
	.openBookEditPageBtn {
		display: flex;
		padding: 10px 30px 10px 30px;
	}
	.bookEmptyPageBtn {
		display: block;
		width: 100%;
	}
	.openBookBtn {
		margin-top: 30px;
	}
	.openBookEmptyPageBtn {
		margin-top: 10px;
	}
	.book {
		width: 300px;
	}
}
