body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
	text-align: center;
	font-family: "Monda", sans-serif;
}

body {
	background-image: url("/Images/woodenDesk.jpg");
	background-repeat: no-repeat;
	background-position: center center;
	background-attachment: fixed;
	background-size: cover;
}

.footer {
	background: black;
	color: rgb(67, 217, 255);
	font-size: 18px;
	position: fixed;
	bottom: 0;
	width: 100%;
	padding: 1rem;
	text-align: center;
}

@media (max-width: 360px) {
	.appHeader {
		display: block !important;
	}

	.logo {
		width: 250px !important;
	}
}

@media (max-width: 436px) {
	.logo {
		margin-left: -60px;
		width: 250px !important;
	}
}

@media (max-width: 562px) {
	.logo {
		max-width: 350px;
	}
}

@media (max-width: 768px) {
	.logo {
		width: 450px;
	}
}

@media (max-width: 991px) {
	/* Styles */
	.logo {
		margin-left: 20%;
	}
}

.appHeader {
	background-color: rgb(5, 0, 0);
	min-width: 100%;
	height: 170px;
	color: white;
}

.logoHome {
	height: 160px;
	width: 400px;
}

.linksNavbar {
	color: rgb(3, 0, 0) !important;
	font-size: 18px;
	margin-left: 240px;
	font-weight: bolder;
}

.usernameLoggedinHome {
	font-size: 20px;
	color: rgb(255, 255, 255);
	text-shadow: 2px 2px #4e370b;
	display: inline-block;
}

.authNavItemNavbar {
	margin-left: 200px;
	width: 100%;
}

.authNavItemNavbarBtn {
	margin-left: 20px;
}

.logoutBtnNavbar {
	color: rgb(4, 3, 37) !important;
	border-radius: 10px;
	background: rgb(77, 196, 243);
	padding: 10px 20px 10px 20px;
	font-size: 16px;
	font-weight: bolder;
	display: inline-block;
}

/* Smartphones (portrait and landscape) ----------- */
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
	/* Styles */
	.logoHome {
		height: 100px;
		width: 340px;
	}

	.authNavItemNavbar {
		margin-left: 0px;
		background: rgba(43, 41, 41, 0.925);
	}

	.authNavItemNavbarBtn {
		margin-left: 0px;
		background: rgba(43, 41, 41, 0.925);
	}

	.linksNavbar {
		color: rgb(252, 249, 249) !important;
		font-size: 18px;
		margin-left: 0px;
		font-weight: normal;
	}

	.usernameLoggedinHome {
		text-align: center;
		font-size: 20px;
		color: rgb(255, 255, 255);
		display: block;
	}
}

@media (max-width: 991px) {
	/* Styles */
	.navItemNavbar {
		width: 100%;
		background: rgba(43, 41, 41, 0.925);
		color: white;
	}
}

@media (max-width: 1025px) {
	/* Styles */
	.usernameLoggedinHome {
		font-size: 18px;
		margin-left: 50px;
	}
}

.signinForm {
	background: rgba(221, 232, 240, 0.801);
	border: 3px solid #1d2227;
	border-radius: 15px;
	margin: 20px;
	padding: 30px;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 40px;
	width: 100%;
}

.signinTitle {
	text-align: center;
	margin-top: 15px;
}

.formBox {
	background: rgba(32, 23, 7, 0.993);
	border: 10px double #f3f1f1;
	margin-top: 40px;
	padding: 20px;
	color: white;
}

.signinPage {
	margin-top: 60px;
	margin-bottom: 80px;
}

@media (max-width: 360px) {
	.signinPage {
		margin-top: 10px;
	}
}

.formBoxSignup {
	background: rgba(32, 23, 7, 0.993);
	border: 10px double #f3f1f1;
	margin-top: 40px;
	padding: 20px;
	color: white;
	width: 400px;
}

.wrapper {
	display: grid;
	grid-auto-rows: minmax(100px, auto);
	grid-template-columns: repeat(3, 1fr);
	background: rgba(226, 223, 173, 0.719);
	padding-bottom: 50px;
	grid-gap: 5px;
}

.homeTitle {
	grid-column-start: 1;
	grid-column-end: 4;
	text-shadow: 4px 4px #4e370b;
	color: white;
	margin-top: 80px;
}
.homeSubTitle {
	text-align: center;
	grid-column-start: 1;
	grid-column-end: 5;
}

.imgRegister {
	width: 270px;
	justify-self: center;
}

.imgDiary1 {
	grid-column: 2 /4;
	width: 550px;
}

.imgDiary2 {
	margin-top: 50px;
	grid-row: 4;
	width: 100%;
	grid-column: 1 /4;
}

/* Smartphones (portrait and landscape) ----------- */
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
	/* Styles */
	.homeTitle {
		font-size: 45px;
		margin-top: 10px;
	}
	.homeSubTitle {
		font-size: 25px;
		margin-top: 10px;
	}

	.wrapper {
		display: flexbox;
	}

	.imgRegister {
		width: 270px;
	}

	.imgDiary1 {
		display: none;
	}

	.imgDiary2 {
		margin-top: 50px;
		width: 100%;
	}
}

.profileMainContainer {
	background: rgba(54, 34, 8, 0.74);
	height: 900px;
	border-radius: 10px;
	border: dashed 2px white;
}

.book {
	height: 670px;
	width: 600px;
	background: rgba(228, 228, 101, 0.719);
	border: 5px dashed white;
	justify-content: center;
	margin-top: 10px;
	margin-bottom: 50px;
	margin-left: auto;
	margin-right: auto;
	border-radius: 10px;
}

.bookCover {
	height: 670px;
	width: 600px;
	background: rgba(228, 228, 101, 0.719);
	border: 5px dashed white;
	margin-top: 10px;
	margin-bottom: 50px;
	margin-left: auto;
	margin-right: auto;
	border-radius: 10px;
}

.messageBeneathBooks {
	color: white;
	margin-top: 100px;
	line-height: 200%;
}

.messageWhenNoContentOnBook {
	color: rgb(94, 87, 67);
	font-size: 24px;
	margin-top: 30px;
}

.editPageTitle {
	text-align: center;
}

.inputEditText {
	color: white;
}

.flipPageComponent {
	display: flex;
	margin-left: 40px;
	border: 4px solid white;
}

.articleContent {
	text-align: left;
	margin-left: 20px;
	margin-right: 20px;
	color: rgb(49, 47, 47);
	font-size: 20px;
}

.pageContent {
	text-align: left;
	margin-left: 20px;
}

.bookContent {
	font-size: 20px;
	color: white;
	text-shadow: 2px 2px #4e370b;
	margin-top: 20px;
}

.openBookBtn {
	margin-top: 80px;
	margin-left: 5px;
	padding: 10px 20px 10px 20px;
	border-radius: 10px;
	background: rgb(126, 99, 9);
	color: white;
	font-size: 18px;
	text-shadow: 2px 2px #000000;
	font-weight: bolder;
}

.deleteBtn {
	margin-left: 5px;
	padding: 10px 20px 10px 20px;
	border-radius: 10px;
	background: rgb(173, 21, 10);
	color: white;
	font-size: 14px;
	text-shadow: 2px 2px #000000;
	font-weight: bolder;
}

.openBookEmptyPageBtn {
	/* margin-top: 80px; */
	margin-left: 5px;
	padding: 10px 20px 10px 20px;
	border-radius: 10px;
	background: rgb(126, 112, 53);
	color: white;
	font-size: 18px;
	text-shadow: 2px 2px #000000;
	font-weight: bolder;
}

.modal-backdrop {
	background-color: transparent;
}

.modal {
	background-color: rgba(255, 255, 255, 0);
	width: 100%;
	min-height: 1000px;
}

.modal-content {
	background-color: rgb(15, 13, 2);
	color: rgb(253, 250, 250);
}

.openBookEditPageBtn {
	margin-top: 20px;
	margin-left: 5px;
	padding: 10px 20px 10px 20px;
	border-radius: 10px;
	background: rgb(32, 150, 21);
	color: white;
	font-size: 14px;
	text-shadow: 2px 2px #000000;
	font-weight: bolder;
}

.bookEmptyPageBtn {
	margin-left: 5px;
	padding: 10px 20px 10px 20px;
	border-radius: 10px;
	background: rgb(126, 112, 53);
	color: white;
	font-size: 14px;
	text-shadow: 2px 2px #000000;
	font-weight: bolder;
}

.profileTitle {
	color: white;
	text-shadow: 4px 4px #4e370b;
	font-size: 40px;
}

.bookInput {
	background: rgb(158, 139, 98) !important;
	height: 470px !important;
	padding: 12px 20px;
	border-radius: 10px;
	border: dotted 2px rgb(54, 38, 7);
	width: 90%;
	margin: auto;
	font-size: 20px;
	color: white;
	text-align: justify;
}

.saveButton {
	padding: 10px 40px 10px 40px;
	font-size: 20px;
	border-radius: 15px;
	background: rgb(241, 121, 35);
	color: white;
	text-shadow: 2px 2px #4e370b;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
	.bookCover {
		width: 300px;
		margin-left: -5px;
	}
	.flipPageComponent {
		margin-left: 10px;
	}
	.profileTitle {
		font-size: 26px;
	}
	.deleteBtn {
		float: left;
	}
	.openBookEditPageBtn {
		display: flex;
		padding: 10px 30px 10px 30px;
	}
	.bookEmptyPageBtn {
		display: block;
		width: 100%;
	}
	.openBookBtn {
		margin-top: 30px;
	}
	.openBookEmptyPageBtn {
		margin-top: 10px;
	}
	.book {
		width: 300px;
	}
}

